@use "sass:map";

.footer {
  margin: 0 4.8vw 1% 4.8vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &_copyright {
    @include text;
    font-size: .95vw;
    padding-bottom: 1%;
  }

  &_links {
    display: flex;
    gap: 1vw;

    &--icon {
      color: map.get($colors, accent-color);
      font-size: 1.4vw;
    }
  }
}

@media (max-width: 792px) {
  .footer {
    display: none;
  }
}